@import "~@hse-design/tokens/lib/scss/breakpoints";
@import "~@hse-design/tokens/lib/scss/spacing";

@media screen and (max-width: $breakpoint-tablet-small) {
  .news {
    display: flex;
    flex-direction: column;
    overflow-x: initial;
    overflow-y: initial;

    & > article {
      width: 100%;
      max-width: 100%;
    }

    & > *:not(:first-child) {
      margin-left: 0;
      margin-top: $spacing-medium-x;
    }
  }
}
